import React from 'react'

class GatoGenerico extends React.Component
{
    //Variables internas
    nombre='';
    indice=0;
    link='./img/GatosGenericos/';
    image = ['000.jpg','001.jpg','002.jpg','003.jpg','004.jpg'];

    //Constructores
    constructor(props)
    {
        super();
        if(props.x<0 || props.x>4){props.x=0;}
        this.indice=props.x;
        this.nombre = props.nombre;
    }

    render()
    {
        return (
            <>
            <p>Nombre: {this.nombre}</p>
            <img src={this.link+this.image[this.indice]} alt={this.link+this.image[this.indice]} id='GatoGenerico'></img>
            </>
        );
    }
}

export default GatoGenerico;