import React from 'react'

const Kitsa = (props) =>
{
    if(props.x <0 || props.x > 19){ props.x = 0;}
    const link = './img/Kitsa/';
    const image=['000.jpg','001.jpg','002.jpg','003.jpg','004.jpg','005.jpg','006.jpg','007.jpg','008.jpg','009.jpg','010.jpg','011.jpg','012.jpg','013.jpg','014.jpg','015.jpg','016.jpg','017.jpg','018.jpg','019.jpg'];
    return (
        <>
        <p>Nombre: {props.name}</p>
        <img src={link+image[props.x]} alt={link+image[props.x]} id='Kitsa'></img>
        </>
    );
}

export default Kitsa;